import { assignedPath } from "@jeff-aporta/router";

// Funcin auxiliar para errores de autenticacin y acceso segn ruta
function authError([first, second]) {
  const thereIsUser = window["currentUser"];
  if (!thereIsUser) {
    if (first === "users") {
      if (second !== "login") {
        return {
          error: true,
          message: "Debes iniciar sesin para acceder a Usuarios",
        };
      }
    }
    if (first === "dev") {
      return {
        error: true,
        message: "Debes iniciar sesin para acceder a Dev",
      };
    }
  }
}

export function routeCheck({
  querypath: path = () => 0, // Por defecto evita errores
}) {

  if (!path) {
    return {}; // No debe retornar error
  }

  const segments = (() => {
    if (Array.isArray(path)) {
      return path;
    }
    return path.split("/").filter(Boolean);
  })();

  return (
    authError(segments) ?? // Encontr error de autenticacin
    {} // No encontr error
  );
}
